<template>
  <div class="goods-item">
    <a :href="goodsItem.item_url">
      <img :src="goodsItem.image" alt="" @load="imageLoad">
      <div class="item-info">
        <p>{{goodsItem.title}}</p>
        <span class="price">{{goodsItem.mlsDiscountPrice}}</span>
        <span class="collect">{{goodsItem.itemSale}}</span>
        <span></span>
      </div>
    </a>
  </div>
</template>

<script>
  export default {
    name: "GoodsItem",
    props: {
      goodsItem: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    methods: {
      /** 解释:
       * 图片加载需要时间,
       * 图片加载完后会把整个内容高度撑起来, 但此时scroll高度不会自动变化
       * 所以需要在图片加载完后重新计算scroll高度
       *    原生的js监听图片: img.onload = function() {}
       *    Vue中监听: @load='方法'
       * 图片加载在GoodsItem.vue中发生, 而scroll高度在Home.vue中计算
       * 便涉及到非父子组件通信的问题,
       * 方法一: Vuex状态管理
       * 方法二: 事件总线 (此处采用方法二)
       * 步骤: 1.使用Vue原型的方法, 注册事件总线:
       *          Vue.prototype.$bus = new Vue()
       *          Vue实例是可以作为事件总线的
       *      2.发射事件:
       *          this.$bus.$emit('事件名称', 参数)
       *      3.接收事件:
       *          this.$bus.$on('事件名称', 回调函数(参数))
       *
       * */
      imageLoad() {
        this.$bus.$emit('itemImageLoad')
      }
    }
  }
</script>

<style scoped>
  .goods-item {
    position: relative;
    padding-top: 10px;
    padding-bottom: 40px;
    width: 48%;
  }

  .goods-item img {
    width: 100%;
    border-radius: 5px;
  }

  .item-info {
    font-size: 12px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    overflow: hidden;
    text-align: center;
  }

  .item-info p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
  }

  .item-info .price {
    color: var(--color-high-text);
    margin-right: 20px;
  }

  .item-info .collect {
    position: relative;
  }

  .item-info .collect::before {
    content: '';
    position: absolute;
    left: -15px;
    top: -1px;
    width: 14px;
    height: 14px;
    background: url("~assets/img/common/collect.svg") 0 0/14px 14px;
  }
</style>
