export function debounce(func, delay) {
  let timer = null;
  return function(...args) {
    let context = this;  //获取函数所在作用域this
    // let args = arguments;  //取得传入参数, 和...args传参一样
    if (timer) clearTimeout(timer);
    timer = setTimeout(() => {
      func.apply(context, args);
    }, delay)
  }
}
