<template>
	<div class="tab-control">
    <div v-for="(item, index) in titles"
         class="tab-control-item"
         :class="{ activeClass: index === currentIndex }"
         @click="itemClick(index)"
    >
      <span>{{item}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TabControl",
    props: {
      titles: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        currentIndex: 0,
      }
    },
    methods: {
      itemClick(index) {
        this.currentIndex = index;
        this.$emit('tableClick', index)
      },
    }
  }
</script>

<style scoped>
  .tab-control {
    display: flex;
    text-align: center;
    height: 40px;
    line-height: 40px;
    font-size: 15px;
  }
  .tab-control-item {
    flex: 1;
  }
  .tab-control-item span {
    padding: 5px;
  }
  .activeClass {
    color: var(--color-high-text);
  }
  .activeClass span {
    border-bottom: 3px solid var(--color-tint);
  }
</style>
