<template>
	<div class="wrapper" ref="wrapper">
    <div class="content"><slot></slot></div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    name: "Scroll",
    props:{
      probeType: {
        type: Number,
        default: 0,
      },
      pullUpLoad: {
        type: Boolean,
        default: false,
      }
    },
    data() {
      return {
        scroll: null,
      }
    },
    mounted() {
      /** 1.创建BScroll对象 */
      // 方法1.1
      // this.scroll = new BScroll('.wrapper', {})
      // 方法1.2
      // this.scroll = new BScroll(document.querySelector('.wrapper'), {})
      // 方法2, this.$refs不仅可以绑定子组件, 也可以绑定普通的DOM元素
      this.scroll = new BScroll(this.$refs.wrapper, {
        click: true, // 必须设置, 否则会阻止vue的原生click事件
        // 无论click是否设置为true, button标签都可以点击
        // 但是当click没设置为true, div等很多其他标签便不能点击
        probeType: this.probeType, // 实时监听滚动的类型, 0(默认)和1都是不监听; 2监听手指滑动,手指离开后不监听; 3则是只要在滑动就监听, 包括手指离开后的惯性滑动
        pullUpLoad: this.pullUpLoad, // 监听BScroll是否下拉到了底部
      })
      /** 2.监听滚动位置 */
      if (this.probeType === 2 || this.probeType===3) {
        this.scroll.on('scroll', (p) => {
          // console.log(p);
          this.$emit('scroll', p)
        })
      }
      /** 3.监听scroll滚动到底部 */
      if (this.probeType === 3) {
        this.scroll.on('pullingUp', () => {
          this.$emit('pullingUp')
        })
      }
    },
    methods: {
      scrollTo(x, y, time=300) {
        this.scroll.scrollTo(x, y, time)
      },
      finishPullUp() {
        this.scroll.finishPullUp()
      },
      refresh() {
        /**
         * 调用this.scroll的方法之前, 判断this.scroll对象是否有值
         * 为什么要这样做?
         * 因为设置了当图片加载完成, 便操作一次 refresh()
         * 因为图片有时加载慢, 有时加载却是很快的, 当图片加载快于scroll生成时,
         * scroll还是undefined, 这时调用它的方法肯定就报错了
         * 所以在前面加多重判断
         *
         * */
        console.log('refresh---');
        this.scroll && this.scroll.scrollTo && this.scroll.refresh()
      }
    }
  }
</script>

