<script>
  import NavBar from "components/common/navbar/NavBar";
  import HomeSwiper from "./childComps/HomeSwiper"; // 轮播图
  import RecommendView from "./childComps/RecommendView"; // 好物推荐
  import FeatureView from "./childComps/FeatureView"; // 本周流行
  import TabControl from "components/content/tabControl/TabControl"; // 商品切换
  import GoodsList from "components/content/goods/GoodsList"; // 商品列表
  import Scroll from "components/common/scroll/Scroll"; // 滑动组件
  import BackTop from "components/content/backTop/BackTop"; // 回到顶部

  import { getHomeMultiData, getHomeGoods } from "network/home";
  import { debounce } from "common/utils";

  // console.log(this) // undefined

  export default {
    name: "Home",
    components: {
      NavBar,
      HomeSwiper,
      RecommendView,
      FeatureView,
      TabControl,
      GoodsList,
      Scroll,
      BackTop,
    },
    data() {
      return {
        // result: null,
        banners: null,
        recommends: null,
        goods: {
          'pop': {page: 0, list: []},
          'news': {page: 0, list: []},
          'sell': {page: 0, list: []}
        },
        currentType: 'pop',
        isShowBackTop: false,
      }
    },
    computed: {
      showGoods: function() {
        return this.goods[this.currentType].list
      }
    },
    created() {
      // 1.请求多个数据
      this.getHomeMultiData();
      // 2.请求商品数据
      this.getHomeGoods('pop');
      this.getHomeGoods('news');
      this.getHomeGoods('sell');
    },
    mounted() {
      // 3.监听GoodsItem中图片加载完成
      // 且此处应该放在mounted而不是created中监听,
      // 因为在created时, DOM还没加载好, $refs很可能找不到对应DOM元素
      const refresh = debounce(this.$refs.scroll.refresh, 100);
      this.$bus.$on('itemImageLoad', () => {
        // this.$refs.scroll.refresh();
        refresh()
      })
      // 4.获取的offsetTop
      // obj.offsetTop 指 obj 距离上方或上层控件的位置，整型，单位像素。普通的DOM元素都有这个属性,
      // 但tabControl是一个大的组件, 需要获取到它里面的具体元素
      // 所有组件都有一个属性$el: 用于获取组件中的元素
      this.vvvClick()
      // console.log(tabControlthis.$refs.vvv.offsetTop);
    },
    methods: {
      vvvClick() {
        console.log(this.$refs.vvv.offsetTop);
      },
      /**
       * 网络请求相关方法
       * */
      getHomeMultiData() {
        getHomeMultiData().then(res => {
          // this.result = res.data;
          this.banners = res.data.banner.list;
          this.recommends = res.data.recommend.list;
        })
      },
      getHomeGoods(type) {
        const page = this.goods[type].page + 1
        getHomeGoods(type, page).then(res => {
          this.goods[type].list.push(...res.data.list);
          this.goods[type].page += 1;
          this.$refs.scroll.finishPullUp();
        })
      },
      /**
       * 自定义事件的监听
       * */
/*      debounce(func, delay) {
        let timer = null;
        // let cnt = 0;
        return function(...args) {
          // console.log(cnt++)
          let context = this;  //获取函数所在作用域this
          // let args = arguments;  //取得传入参数, 和...args传参一样
          if (timer) clearTimeout(timer);
          timer = setTimeout(() => {
            func.apply(context, args);
          }, delay)
        }
      },*/
      tableClick(index) {
        // 监听时没写参数的话, 默认会把附带的参数传进来
        // console.log(event, '---event');
        switch(index) {
          case 0:
            this.currentType = 'pop';
            break;
          case 1:
            this.currentType = 'news';
            break;
          case 2:
            this.currentType = 'sell';
            break;
        }
      },
      contentScroll(p) {
        // console.log(p);
        this.isShowBackTop = -p.y > 1000;
      },
      loadMore() {
        this.getHomeGoods(this.currentType);
        // this.$refs.scroll.refresh();
      },
      /**
       * 原生事件的监听
       * */
      backTopClick() {
        // 直接调BScroll原生的scrollTo方法:
        // this.$refs.scroll.scroll.scrollTo(0, 0, 500);
        // 调scroll组件中自己封装的scrollTo方法:
        this.$refs.scroll.scrollTo(0, 0, 500);
      }
    }
  }
</script>
